<template>
  <div class="root" :style="{ height: v_height }">
    <video
      autoplay
      playsinline
      muted
      controls
      id="wowza-receiver"
      type="module"
      src="../utils/webrtc/play"
    ></video>
  </div>
</template>

<script>
import { EventBus, EVENT_PLAY_WEBRTC, EVENT_SHOW_SNACKBAR } from "../utils/events-bus";
import * as Receiver from "../utils/webrtc/play";

export default {
  data() {
    return {
      v_height: "0px"
    };
  },
  created() {
    const calculate_height = (window.innerWidth * 9) / 16;
    this.v_height = calculate_height + "px";

    const onShow = message => {
      EventBus.$emit(EVENT_SHOW_SNACKBAR, message);
    };

    const onHide = () => {
      console.log("### onHide");
    };

    const time = setTimeout(() => {
      Receiver.initialize(onShow, onHide);
      clearTimeout(time)
    }, 3000)

    EventBus.$on(EVENT_PLAY_WEBRTC, () => {
      console.log("PLAY");
    });
  }
};
</script>

<style scoped>
.root {
  position: relative;
  background: black;
}

#wowza-receiver {
  background: black;
  width: 100% !important;
  height: auto !important;
}
</style>