<template>
  <div class="root" :style="{ height: v_height }">
    <video id="wowza-sender" autoplay playsinline muted controls></video>
  </div>
</template>

<script>
import * as Sender from "../utils/webrtc/publish";
import {
  EventBus,
  EVENT_PUBLISH_WEBRTC,
  EVENT_SHOW_SNACKBAR
} from "../utils/events-bus";

export default {
  data() {
    return {
      v_height: "0px"
    };
  },
  created() {
    const calculate_height = (window.innerWidth * 9) / 16;
    this.v_height = calculate_height + "px";

    const onShow = message => {
      EventBus.$emit(EVENT_SHOW_SNACKBAR, message);
    };

    const onHide = () => {
      console.log("### onHide");
    };

    this.$nextTick(() => {
      console.log("Sender Created");
      Sender.initialize(onShow, onHide);
    });

    EventBus.$on(EVENT_PUBLISH_WEBRTC, () => {
      console.log("RECEIVE PUBLISH");
    });
  }
};
</script>

<style scoped>
.root {
  position: relative;
}

#wowza-sender {
  background: black;
  width: 100% !important;
  height: auto !important;
}
</style>