<template>
  <div class="app">
    <router-view />
    <v-snackbar v-model="snackbar" :multi-line="multiLine" centered>
      {{ text }}
    </v-snackbar>
  </div>
</template>

<script>
import * as CONFIG from "./utils/config";
import { EventBus, EVENT_SHOW_SNACKBAR } from "./utils/events-bus";

export default {
  data: () => ({
    multiLine: true,
    snackbar: false,
    text: `I'm a multi-line snackbar.`,
    timeout: 3000
  }),
  created() {
    CONFIG.initialize();
    EventBus.$on(EVENT_SHOW_SNACKBAR, msg => {
      this.snackbar = true;
      this.text = msg;
    });
  },
  methods: {
    showError() {
      this.snackbar = true;
    }
  }
};
</script>

<style>
* {
  font-family: "Nanum Gothic", "Roboto";
}

.app {
  background-color: black;
}
</style>