<template>
  <div class="root">
    <div class="shadow">
      <tab-indicator
        :props_event_type="this.EVENT_SELECT_TOP_TAP"
        :props_tab_length="this.video_items.length"
      />
      <div class="horizontal_line">
        <p class="font">I M FINE Web App</p>
        <div class="ver">VERSION: {{this.app_version}}</div>
      </div>
      <tab-indicator
        :props_event_type="this.EVENT_SELECT_BOTTOM_TAP"
        :props_tab_length="this.tab_items.length"
      />
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import TabIndicator from "@/components/TabIndicator.vue";
import {
  EVENT_SELECT_BOTTOM_TAP,
  EVENT_SELECT_TOP_TAP
} from "../utils/events-bus";
import { version } from "../../package.json";

export default {
  data() {
    return {
      EVENT_SELECT_BOTTOM_TAP,
      EVENT_SELECT_TOP_TAP,
      app_version: 0
    };
  },
  components: {
    TabIndicator
  },
  computed: {
    ...mapState(["tab_items", "video_items"])
  },
  created() {
    this.app_version = version
  },
};
</script>

<style scoped>
.font {
  position: relative;
  text-align: center;
  padding-top: 5px;
  font-size: 18px;
  font-weight: bold;
  font-family: "Roboto", sans-serif;
}

.shadow {
  box-shadow: 0px 1px 10px lightgray;
  padding-bottom: 10px;
}

.horizontal_line {
  display: flex;
  align-items: center;
  justify-content: center;
}

.ver {
  position: absolute;
  right: 0;
  margin-right: 20px;
  font-size: 12px;
}
</style>